import Vue from 'vue'
import Router from 'vue-router'
import home from '../components/home/home'
import word from '../components/word/word'
import pic from '../components/pic/pic'
import detail from '../components/detail'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'//
    },
    {
      path: '/home',
      name:'home',
      component: home,
      meta: {
        keepAlive: true, //
      }
    },
    {
      path: '/word',
      name:'word',
      component: word,
      meta: {
        keepAlive: true, //
      }
    },
    {
      path: '/pic',
      name:'pic',
      component: pic,
      meta: {
        keepAlive: true, //
      }
    },
    {
      path: '/detail',
      name:'detail',
      component: detail,
      meta: {
        keepAlive: false, //
      }
    },
    ]
})



