<template>
  <div class="container">
    <div class="left col-sm-2">
      <div class="ad">
        <p style="font-size: 20px">C</p>
        <img src="../../static/img/ad2.jpg"class="ad_img">
      </div>
    </div>
  <div class="center col-sm-7 ">
    <div class="loading" v-if="!article.content">
      <img src="../../static/img/loading.gif" alt="" class="image" mode="aspectFit">
    </div>
    <YdList :datas="detail_data" :user="user" :article="article" :votes="votes"/>
    <div v-if="hot_comments.length>0" class="hotcomments">
      <i class="hot">:{{ hot_comments.length}}</i>
      <Comments :hot="hot_comments"/>
    </div>
    <div v-if="comment_count>0" class="newcomments">
      <i class="new">:{{new_comments_counts}}</i>
      <Comments :hot="new_comments"/>
    </div>
    <div class="no_comment" v-if="new_comments_counts===0">
      <i></i>
      <img src="../../static/img/sofa.png" class="sofa" >
    </div>
  </div>
    <div class="right_content col-sm-3" ref="rightContent">
      <div class="sidebar-inform">
        <a href="http://report.12377.cn:13225/toreportinputNormal_anis.do" target="_blank" rel="nofollow" onclick="_hmt.push(['_trackEvent','web-sidebar-jubao','chick'])"></a>
      </div>
      <div class="sidebar-other">
        <img src="https://www.qiushibaike.com/static/images/productlist/ctrl_d.png" alt="">
        <p>YD</p>
      </div>
      <div class="sidebar-hot clearfix" id="sidebar-qrcode">
        <div class="float-left qrcode">
          <img src="../../static/img/duanzi.png" alt="YD ">
        </div>
        <div class="float-left btn-box">
          <a href="javascript:void(0)" class="btn-download android" onclick="window.open('https://v.qq.com/x/page/d0702awp30g.html')" target="_blank"></a>
          <p class="tips">YD,</p>
        </div>
        <div class="float-left connect">
          <img src="../../static/img/weixin.png" alt="">
          <img src="../../static/img/qq.jpg" alt="">
        </div>
      </div>
    </div>
</div>
</template>
<script>
  import axios from 'axios'
  import YdList from '../components/YdList'
  import Comments from '../components/comments'
export default {
  name: 'HelloWorld',
  data () {
    return {
      API_PROXY : 'https://bird.ioliu.cn/v1/?url=',
      detail_data:{},
      user:{},
      article:{},
      votes:{},
      hot_comments:[],
      new_comments:[],
      new_comments_counts:"",
      children:[],
      comment_count:""
    }
  },
  components:{
    YdList,
    Comments
  },
  updated() {
    window.scroll(0, 0);
  },
 activated(){
   this.detail_data={}
   this.user={}
   this.article={}
   this.votes={}
   this.hot_comments=[]
   this.new_comments=[]
   this.new_comments_counts=""
   this.children=[]
   this.comment_count=""
   const id=this.$route.params.id;
   const count=this.$route.params.comment_count;
   this.comment_count=count
   this.getData(id)

   window.onscroll = function(){
     //scrollTop
     var scrollTop = document.documentElement.scrollTop||document.body.scrollTop;
     if(scrollTop>90&&this.new_comments.length>5){
       $(".right_content").css({"position":"fixed","width":"18%","right":"258px","top":"0px"})
     }
     else {
       $(".right_content").css({"position":"static","width":"25%","top":"70px"})
     }
   }
 },
  methods:{
    async getData(id){
      const url=this.API_PROXY+"https://m2.qiushibaike.com/v2/article/"+id+"/comment/list?page=1&count=50"
      const res= await axios.get(url);
      this.detail_data=res.data
      this.article=res.data.article
      this.votes=res.data.article.votes
      this.hot_comments=res.data.hot_comments;
      this.new_comments_counts=res.data.total;
      this.new_comments=res.data.items;
      this.user=res.data.article.user
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.container{
  height: 100%;
  width: 100%;
  overflow: hidden;
}

.ad{
  max-width: 223px;
}

.ad img{
  width: 100%;
}

.hot{
  margin: 5px 0 5px 15px;
  font-size: 14px;
  color: red;
}
.new{
  margin: 5px 0 5px 15px;
  font-size: 14px;
  color: #ffa70d;
}
.loading{
  width: 100%;
  height: 1000px;
}

.image{
  width: 50px;
  height: 50px;
  margin: 50% 50%;
}

.no_comment{
  width: 100%;
  height: auto;

}

.sofa{
  width: 84px;
  height: 84px;
  margin-top: 20%;
  margin-left: 40%;
  transform: translate(-50%,-50%);
}


.sidebar-inform {
  width: 305px;
  height: 70px;
  margin-bottom: 16px;
  background: #fff;
}

.sidebar-inform a {
  display: inline-block;
  width: 100%;
  height: 100%;
  background: url('../../static/img/inform.png') center no-repeat;
  background-size: contain;
}
.sidebar-other {
  height: 24px;
  margin-bottom: 16px;
  font-size: 0;
}
.sidebar-other img {
  display: inline-block;
  width: 91px;
  height: 24px;
  margin-right: 10px;
  vertical-align: middle;
}
.sidebar-other p {
  display: inline-block;
  margin: 0;
  height: 24px;
  line-height: 24px;
  font-size: 12px;
  color: #aaa;
  vertical-align: middle;
}
#sidebar-qrcode {
  width: 305px;
  height: 116px;
  padding: 2px;
}
#sidebar-qrcode .qrcode {
  width: 116px;
  height: 116px;
  margin-right: 15px;
}

#sidebar-qrcode .float-left {
  float: left;
}

#sidebar-qrcode .qrcode>img {
  display: block;
  width: 100%;
  height: 100%;
}
.sidebar-hot {
  padding-bottom: 3px;
  background-color: #fff;
}
#sidebar-qrcode .btn-box {
  width: 152px;
}

#sidebar-qrcode .float-left {
  float: left;
}

#sidebar-qrcode .btn-box .android {
  background-color: #50b9f8;
}

#sidebar-qrcode .btn-box .btn-download {
  display: block;
  width: 100%;
  height: 29px;
  color: #fff;
  margin-bottom: 10px;
  border-radius: 4px;
  text-align: center;
  font-size: 14px;
  line-height: 29px;
}
#sidebar-qrcode .btn-box .tips {
  text-align: center;
  font-size: 12px;
  color: #aaa;
}
.btn-box{
  margin-top: 10%;
}

.right_content{
  position: static;
  top: 70px;
  right: 150px;
}

.connect{
  margin-top: 20px;
  width: 305px;
}
.connect img{
  width: 100%;
}

@media (min-width: 1200px){
  .container {
    width: 1370px;
  }
}

</style>



