<template>
  <div class="container">
    <ul>
      <li v-if="items.format==='word'||items.format==='image'" @click="selectItem(items)" class="item" v-for="(items,index) in datas.items" :key="index">
        <div class="publish">
          <div   class="header">
            <img v-if="items.user==null" class="avatar" src="../../static/img/unlogin.png"/>
            <img v-else class="avatar" :src="items.user.thumb"/>
            <div v-if="items.user" class="userInfo">
              <span class="username">{{items.user.login}}</span>
            </div>
          </div>
          <div class="content">
             <div v-if="items.format==='word'" class="word">
               <p>{{items.content}}</p>
             </div>
            <div v-if="items.format==='image'" class="image">
              <p>{{items.content}}</p>
              <img :src="items.high_loc" alt="" class="image" mode="aspectFill">
              <i class="big_pic"></i>
            </div>
          </div>
          <div  class="footer">
            <div class="left">
              <div class="like">
              <img src="../../static/img/happy.png" alt="" class="small_pic">
              <i v-if="items.votes.up!=undefined">+{{items.votes.up}}</i>
              </div>
              <div class="dislike">
              <img src="../../static/img/unhappy.png" alt="" class="small_pic">
                <i v-if="items.votes.down">{{items.votes.down}}</i>
              </div>
            </div>
            <div class="right_comment">
              <div class="comment">
              <img src="../../static/img/comment.png" alt="" class="small_pic">
               <i>{{items.comments_count}}</i>
              </div>
              <div class="share">
              <img src="../../static/img/Share.png" alt="" class="small_pic">
              <i>{{items.share_count}}</i>
              </div>
            </div>
          </div>
          <div v-if="items.hot_comment!=null" class="topcomment">
            <i class="comment_content">{{items.hot_comment.content}}</i>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props:["datas"],
  data(){
    return{
      API_PROXY : 'https://bird.ioliu.cn/v1/?url=',
    }
  },
  methods:{
    selectItem(items){
      const id=items.id;
      const comment_count=items.comments_count;
      this.$router.push({
        path: `/detail`,
        name:'detail',
        params: {
          id: id,
          comment_count:comment_count
        }
      })
    },
  }
}
</script>

<style scoped>
  .container {
    width: 100%;
    height: 100%;
  }
  .item {
    /*display: flex*/
    position: relative;
    padding: 0px 0px 5px;
    height: auto;
    overflow: hidden;
    background: #fafafa;
    border-bottom: 10px solid #f0f0f0;
    border-radius: 5px;
  }

  .publish {
    width: 100%;
    height: auto;
    text-align: center;
  }

  .header {
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  .avatar {
    margin: 5px;
    width: 45px;
    height: 45px;
    border-radius: 30px;
  }

  .userInfo {
    text-align: left;
    margin: 5px 0 0 10px;
    font-size: 14px;
  }

  .content {
    width: 98%;
    height: auto;
    margin: 0 auto;
    text-align: left;
    overflow: hidden;
  }

  p{
    width: 98%;
    margin: 10px auto 0;
    font-size: 16px;
  }

  .pic{
    display: block;
    width: 98%;
    height: 425px;
    margin: 0 auto;
  }

  .image{
    display: block;
    width: 98%;
    margin: 0 auto;
    position: relative;
  }

  .play{
    position: absolute;
    top: 52%;
    left: 50%;
    transform: translate(-50%,-52%);
    width: 50px;
    height: 50px;
  }



  .footer{
    height: 40px;
    width: 95%;
    margin: 15px;
  }

  .small_pic{
    height: 24px;
    width: 24px;
    margin: 5px 5px;
  }

  .left{
    display: flex;
    flex-direction: row;
    float: left;
  }
  .right_comment{
    display: flex;
    flex-direction: row;
    float: right;
  }

  .like{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-right: 12px;
  }
  .dislike{
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  .share{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-left: 12px;
  }
  .comment{
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  i{
    font-size: 14px;
  }
  .topcomment{
    display: flex;
    flex-direction: row;
    align-items: left;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    overflow: hidden;
    margin: 25px 10px 9px;
    text-align: left;
  }

  .comment_content:before{
    content: url("../../static/img/fire.png");
  }

  .multi{
    position: relative;
  }
  .gif{
    background-color: #666;
    color: white;
    padding: 3px;
    border-radius: 5px;
    position: absolute;
    top: 25px;
    right: 16px;
  }

  .image{
    position: relative;
  }
  .big_pic{
    background-color: #666;
    color: white;
    padding: 3px;
    border-radius: 5px;
    position: absolute;
    top: 25px;
    right: 16px;
  }

  p,img:hover{
    cursor: pointer;
  }


</style>



