<template>
    <div class="container">
      <ul>
        <li class="item" v-for="(items,index) in hot" :key="index">
          <div class="publish">
            <div class="icon">
              <img v-if="items.user==null" class="avatar" src="../../static/img/unlogin.png"/>
              <img v-else :src="items.user.thumb"  class="avatar"/>
            </div>
            <div class="content">
              <div class="header">
                <div v-if="items.user" class="userInfo">
                  <span class="username">{{items.user.login}}</span>
                  <span class="like_count">{{items.like_count}}</span>
                  <img src="../../static/img/like.png" alt="" class="like">
                </div>
              </div>
              <div class="content_data">
               {{items.content}}
             </div>

              <div v-if="items.children.length>0" class="children">
                <ul>
                  <li  v-for="(child,num) in items.children" :key="num">
                    <div class="children_comment">
                      <div class="user">
                        <i>{{child.user.login}}</i>: {{child.content}}
                      </div>
                    </div>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: "comments",
        props:{
          hot:{
            type:Array,
            default:[]
          },
        }
    }
</script>

<style scoped>
  .container {
    width: 100%;
    height: 100%;
  }
  ul{
    padding-left: 0;
  }
  .item {
    /*display: flex*/
    position: relative;
    padding: 10px 0px 5px;
    height: auto;
    overflow: hidden;
    background: #fafafa;
    border-bottom: 1px solid #f0f0f0;
  }

  .publish {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: auto;
    text-align: center;
  }

  .icon{
    width: 45px;
    height: 100%;
  }

  .avatar {
    margin: 5px;
    width: 45px;
    height: 45px;
    border-radius: 30px;
  }

  .content{
    width: 98%;
    height: auto;
    margin: 0 10px 0 25px;
  }

  .header {
    width: 100%;
    display: flex;
    flex-direction: row;
  }


  .userInfo {
    width: 95%;
    text-align: left;
    font-size: 14px;
  }

  .like_count{
    float: right;
  }

  .like{
    float: right;
    height: 20px;
    width: 20px;
  }

  .content_data{
    font-size: 14px;
    text-align: left;
    margin-top: 5px;
  }

  .username{
    color: #bbb;
  }

  .children{
    width: 95%;
    background-color: #eee;
    padding: 5px;
    margin: 8px 0;
  }
  .children_comment{
    font-size: 12px;
    text-align: left;
  }
  i{
    display: inline-block;
    color: cornflowerblue;
  }

</style>



