<template>
  <div class="container">
    <div v-if="article.format==='word'||article.format==='image'" class="item">
      <div class="publish">
        <div class="header">
          <img v-if="user==null" class="avatar" src="../../static/img/unlogin.png"/>
          <img v-else class="avatar" :src="user.thumb"/>
          <div v-if="user" class="userInfo">
            <span class="username">{{user.login}}</span>
          </div>
        </div>
        <div class="content">
          <div v-if="article.format==='word'" class="word">
            <p>{{article.content}}</p>
          </div>

          <div v-if="article.format==='image'" class="image">
            <p>{{article.content}}</p>
            <img :src="article.high_loc" alt="" class="image" mode="widthFix">
          </div>

        </div>
        <div class="footer">
          <div class="left">
            <div class="like">
              <img src="../../static/img/happy.png" alt="" class="small_pic">
              <i v-if="votes.up">+{{votes.up}}</i>
            </div>
            <div class="dislike">
              <img src="../../static/img/unhappy.png" alt="" class="small_pic">
              <i v-if="votes.down">{{votes.down}}</i>
            </div>
          </div>
          <div class="right">
            <div class="share">
              <img src="../../static/img/Share.png" alt="" class="small_pic">
              <i>{{article.share_count}}</i>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    props:{
      datas:{
        type:Object,
        default:{}
      },
      user:{
        type:Object,
        default:{}
      },
      article:{
        type:Object,
        default:{}
      },
      votes:{
        type:Object,
        default:{}
      },
    },
  }
</script>

<style scoped>
  .container {
    width: 100%;
    height: 100%;
  }

  .item {
    /*display: flex*/
    position: relative;
    padding: 10px 0px 5px;
    height: auto;
    overflow: hidden;
    background: #fafafa;
    border-bottom: 10px solid #f0f0f0;
  }

  .publish {
    width: 100%;
    height: auto;
    text-align: center;
  }

  .header {
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  .avatar {
    margin: 5px;
    width: 45px;
    height: 45px;
    border-radius: 30px;
  }

  .userInfo {
    text-align: left;
    margin: 5px 0 0 10px;
    font-size: 14px;
  }

  .content {
    width: 98%;
    height: auto;
    margin: 0 auto;
    text-align: left;
    overflow: hidden;
  }

  p{
    width: 98%;
    margin: 10px auto 0;
    font-size: 16px;
  }

  .pic{
    display: block;
    width: 98%;
    height: 450px;
    margin: 0 auto;
  }

  video{
    display: block;
    width: 98%;
    height: 450px;
    margin: 0 auto;
  }

  .image{
    display: block;
    width: 98%;
    height: auto;
    margin: 0 auto;
    position: relative;
  }


  .footer{
    height: 40px;
    width: 95%;
    margin: 15px;
  }

  .small_pic{
    height: 24px;
    width: 24px;
    margin: 5px 5px;
  }

  .left{
    display: flex;
    flex-direction: row;
    float: left;
  }
  .right{
    display: flex;
    flex-direction: row;
    float: right;
  }

  .like{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-right: 12px;
  }
  .dislike{
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  .share{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-left: 12px;
  }

  i{
    font-size: 14px;
  }

</style>



