<template>
  <div class="hello">
   <div class="container">
     <div class="left col-sm-2">
       <div class="ad">
         <p style="font-size: 20px"></p>
         <img src="https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=2274887568,2282720856&fm=27&gp=0.jpg"class="ad_img">
       </div>
     </div>
     <div class="center col-sm-7">
       <div class="loading" ref="loading">
         <img src="https://static.oschina.net/uploads/img/201409/26073950_YyYz.gif" >
       </div>
       <card :datas="data" ></card>
     </div>
     <div class="right_content col-sm-3" ref="rightContent">
       <div class="sidebar-inform">
         <a href="http://report.12377.cn:13225/toreportinputNormal_anis.do" target="_blank" rel="nofollow" onclick="_hmt.push(['_trackEvent','web-sidebar-jubao','chick'])"></a>
       </div>
       <div class="sidebar-other">
         <img src="https://www.qiushibaike.com/static/images/productlist/ctrl_d.png" alt="">
         <p>YD</p>
       </div>
       <div class="sidebar-hot clearfix" id="sidebar-qrcode">
         <div class="float-left qrcode">
           <img src="../../../static/img/duanzi.png" alt="YD ">
         </div>
         <div class="float-left btn-box">
           <a href="javascript:void(0)" class="btn-download android" onclick="window.open('https://v.qq.com/x/page/d0702awp30g.html')" target="_blank"></a>
           <p class="tips">YD,</p>
         </div>
         <div class="float-left connect">
           <img src="../../../static/img/weixin.png" alt="">
           <img src="../../../static/img/qq.jpg" alt="">
         </div>
       </div>
     </div>
   </div>
  </div>
</template>
<script>
  import axios from 'axios'
  import Card from '../../components/card'
  export default {
    data () {
      return {
        page:1,
        temp_data:{},
        data:{},
        API_PROXY : 'https://bird.ioliu.cn/v1/?url=',
        flag:true
      }
    },
    components: {
      Card
    },
    created(){
      this.getData(this.flag)
    },
    activated(){
      const that=this
      window.onscroll = function(){
        //scrollTop
        var scrollTop = document.documentElement.scrollTop||document.body.scrollTop;
        //windowHeight
        var windowHeight = document.documentElement.clientHeight || document.body.clientHeight;
        //scrollHeight
        var scrollHeight = document.documentElement.scrollHeight||document.body.scrollHeight;
        if(scrollTop>100){
          $(".right_content").css({"position":"fixed","width":"18%","right":"258px","top":"0px"})
        }
        else {
          $(".right_content").css({"position":"static","width":"25%","top":"70px"})
        }
        //
        if(scrollTop+windowHeight==scrollHeight){
          ++this.page
          //
          that.LoadMore()
        }
      }
    },
    methods: {
      async getData(flag) {
        let url = ""
        url = this.API_PROXY+"https://m2.qiushibaike.com/article/list/text?page="+this.page
        if(flag){
          this.temp_data=await axios.get(url)
          this.data= this.temp_data.data
          console.log(this.data)
          this.flag=false
          this.$refs.loading.style.display="none"
        }
        else {
          ++this.page
          const tem_data=await axios.get(url)
          if(tem_data){
            this.data.items=this.data.items.concat(tem_data.data.items)
            this.$refs.loading.style.display="none"
          }
        }
      },
      LoadMore(){
        this.$refs.loading.style.display="flex"
        this.getData()
      },
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.loading{
  left: 50%;
  position: fixed;
  top: 50%;
  transform: translate(-50%,-50%);
  z-index: 25;
  display: flex;
  align-items: center;
  justify-content: center;
}

.ad{
  max-width: 223px;
}

.ad img{
  width: 100%;
}

.sidebar-inform {
  max-width: 309px;
  width: 100%;
  height: 70px;
  margin-bottom: 16px;
  background: #fff;
}

.sidebar-inform a {
  display: inline-block;
  width: 100%;
  height: 100%;
  background: url('../../../static/img/inform.png') center no-repeat;
  background-size: contain;
}
  .sidebar-other {
    height: 24px;
    margin-bottom: 16px;
    font-size: 0;
  }
.sidebar-other img {
  display: inline-block;
  width: 91px;
  height: 24px;
  margin-right: 10px;
  vertical-align: middle;
}
.sidebar-other p {
  display: inline-block;
  margin: 0;
  height: 24px;
  line-height: 24px;
  font-size: 12px;
  color: #aaa;
  vertical-align: middle;
}
#sidebar-qrcode {
  max-width: 305px;
  height: 116px;
  padding: 2px;
}
#sidebar-qrcode .qrcode {
  width: 116px;
  height: 116px;
  margin-right: 15px;
}

#sidebar-qrcode .float-left {
  float: left;
}

#sidebar-qrcode .qrcode>img {
  display: block;
  width: 100%;
  height: 100%;
}
.sidebar-hot {
  padding-bottom: 3px;
  background-color: #fff;
}
#sidebar-qrcode .btn-box {
  width: 152px;
}

#sidebar-qrcode .float-left {
  float: left;
}

#sidebar-qrcode .btn-box .android {
  background-color: #50b9f8;
}

#sidebar-qrcode .btn-box .btn-download {
  display: block;
  width: 100%;
  height: 29px;
  color: #fff;
  margin-bottom: 10px;
  border-radius: 4px;
  text-align: center;
  font-size: 14px;
  line-height: 29px;
}
#sidebar-qrcode .btn-box .tips {
  text-align: center;
  font-size: 12px;
  color: #aaa;
}
  .btn-box{
    margin-top: 10%;
  }

  .right_content{
    width: 335px;
    position: static;
    top: 70px;
    right: 150px;
  }

  .connect{
    margin-top: 20px;
    width: 305px;
  }
.connect img{
  width: 100%;
}

</style>



